.centrer {
  display: flex;
  justify-content: center;
  align-items: center;
}



.slideInOut {
  animation: slideInOut 8s ease infinite;
}

@keyframes slideInOut {
    0% {
      transform: translateX(0);
    }
    50% {
      transform: translateX(500%);
    }
    100% {
      transform: translateX(0);
    }
  }
  
