@tailwind base;
@tailwind components;
@tailwind utilities;

.styleLogo {
  @apply rounded-full h-8 w-8 hover:cursor-pointer hover:border hover:border-yellow-500;
}
.cards {
  @apply flex md:flex-row flex-col flex-wrap gap-10 justify-center mx-auto w-10/12 content-evenly mt-2  text-quatro;
}
.card {
  @apply flex flex-col bg-secondary shadow-black shadow w-full md:w-1/2 lg:w-1/3  justify-between  rounded-2xl overflow-hidden hover:scale-105;
}
.cardDescription {
  @apply text-start text-xl py-2  flex items-center my-2 justify-between flex-wrap px-6;
}
.imgCard {
  @apply h-64 w-full overflow-hidden border-x-primary shadow-black shadow-sm;
}
.buttonCard {
  @apply w-full  bg-quatro text-white hover:bg-white hover:text-quatro hover:uppercase py-2 shadow-black shadow;
}
.imgLangages {
  @apply flex gap-2;
}
.divLogoLanguage {
  @apply w-2/3 lg:w-1/3 mx-auto grid lg:grid-cols-4 md:lg:grid-cols-3 grid-cols-2 grid-rows-5 lg:gap-8 gap-4;
}
.logoLanguage {
  @apply h-20 mx-auto mb-8;
}

.hrDesign {
  @apply h-1 md:h-1.5 mt-1 md:mt-2 w-1/6 md:w-2/12 bg-yellow-500 border border-yellow-600 rounded-xl ml-0 mr-auto;
}

.h2Design {
  @apply text-quatro py-12 text-2xl mx-auto;
}
